<template>
  <div>
    <h2>其他进制数转换成十进制数</h2>
    <p>方法：把每位数乘权展开，相加即可得到对应的十进制数</p>
    <div class="main">
      <div>
        <h3>二进制转十进制</h3>
        <div class="item">
          (111.01)<sub>2</sub>=1x2<sup>2</sup>+1x2<sup>1</sup>+1x2<sup>0</sup>+0x2<sup>-1</sup>+1x2<sup>-2</sup>=(7.25)<sub>10</sub>
        </div>
      </div>
      <div>
        <h3>十六进制转十进制</h3>
        <div class="item">
          (A2B)<sub>16</sub>=10x16<sup>2</sup>+2x16<sup>1</sup>+11x16<sup>0</sup>=2560+32+11=(2603)<sub>10</sub>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    name: "ToD"
  }
